<template>
  <div class="app-container">
    <!-- Header头部区域 -->
    <Header></Header>

    <!-- 循环渲染每一个商品信息 -->
    <Goods
      v-for="item in list"
      :key="item.id"
      :id="item.id"
      :title="item.goods_name"
      :pic="item.goods_img"
      :price="item.goods_price"
      :state="item.goods_state"
      :count="item.goods_count"
      @state-change="getNewState"
    >
    <!-- <Counter ></Counter> -->
    </Goods>

    <!-- Footer区域 -->
    <Footer
      :isFull="fullState"
      :amount="amt"
      :all="total"
      @full-change="getFullState"
    ></Footer>
  </div>
</template>

<script>
// 导入axios请求库
import axios from "axios";
// 导入需要组件
import Header from "@/components/Header/Header.vue";
import Goods from "@/components/Goods/Goods.vue";
import Footer from "@/components/Footer/Footer.vue";
// import Counter from "@/components/Counter/Counter.vue"

import bus from "@/components/eventBus.js";

export default {
  components: {
    Header,
    Goods,
    Footer,
    // Counter
  },
  methods: {
    // 封装请求列表数据的方法
    async initCartList() {
      // 调用axios的get方法请求列表数据
      const { data: res } = await axios.get("https://www.escook.cn/api/cart");
      // 只要请求回来的数据，在页面渲染期间要用到，则必须转存到data中
      if (res.status === 200) {
        this.list = res.list;
      }
    },
    getNewState(e) {
      // 接受子组件传递过来数据
      // e的格式为{id，value}
      this.list.some((item) => {
        if (item.id === e.id) {
          item.goods_state = e.value;
          // 终止后续循环
          return true;
        }
      });
    },
    getFullState(e) {
      // 接受footer子组件传递过来的全选按钮状态
      this.list.forEach((item) => (item.goods_state = e));
    },
  },

  data() {
    return {
      // 用来存储购物车的列表数据，默认为空数组
      list: [],
    };
  },
  // 计算属性
  computed: {
    fullState() {
      // 动态计算出全选状态
      return this.list.every((item) => item.goods_state);
    },
    amt() {
      // 已勾选商品的总价格
      return this.list
        .filter((item) => item.goods_state)
        .reduce(
          (total, item) => (total += item.goods_price * item.goods_count),
          0
        );
    },
    total() {
      // 已勾选商品的总数量
      return this.list
        .filter((item) => item.goods_state)
        .reduce((t, item) => (t += item.goods_count), 0);
    },
  },
  created() {
    // 调用请求数据方法
    this.initCartList();

    bus.$on("share", (val) => {
      this.list.some((item) => {
        if (item.id === val.id) {
          item.goods_count = val.value;
          return true;
        }
      });
    });
  },
};
</script>

<style lang="less" scoped>
.app-container {
  padding-top: 45px;
  padding-bottom: 50px;
}
</style>
